.table {
  margin-top: 32px;
  border: 1px solid #203B60;
  border-bottom: 0;
  background-color: rgba(85, 119, 253, 0.1);  
  box-shadow: none;  
  .rc-table-container {
    overflow: hidden;
  }
  table {
    width: 100%;
    border-collapse: collapse;
  }
  .t-title {
    color: #ffffff;
    font-size: 32px;
    padding: 24px 24px;
    border-bottom: 1px solid #203B60;
  }
  .rc-table-thead {
    border-bottom: 1px solid #203B60;
    color : #93A1B8;
    .rc-table-cell{
      white-space: nowrap;
      padding: 32px 0;
    }
  }
  .rc-table-tbody {
    color: #ffffff;
    .rc-table-row {
      height: 48px;
    }
    .rc-table-cell{
      border:0px;
      text-align: center;
      white-space: nowrap;
    }
    .rc-table-row:nth-child(odd){
      background: rgba(85, 119, 253, 0.1);
      border-top: 1px solid #203B60;
      border-bottom: 1px solid #203B60;
    }
    .rc-table-row .rc-table-cell-row-hover{
      border-top: 1px solid rgba(205, 122, 55, 0.5);
      border-bottom: 1px solid rgba(205, 122, 55, 0.5);
      cursor: pointer;
    }
  }
  .rc-table-footer {
    display: flex;
    justify-content: center;
    align-items: center;
    
  }
}


@media only screen and (max-width: 760px) {
  .table {
    margin-top: 32px;
    border: 1px solid #203B60;
    background-color: rgba(85, 119, 253, 0.1);
    .rc-table-container {
      overflow: auto;
      font-size: 12px;
    }
  }
  .table {
    box-shadow: inset -10px 0 8px -8px #203B60;
  }
}