.info-main {
  font-family: Barlow;
  font-style: normal;
  font-weight: normal;
  color: #FFFFFF;
  font-size: 16px;
  padding: 0 32px;
  margin-top: 50px;
  .title {
    font-size: 32px;
    a:hover {
      text-decoration: underline;
    }
  }
  .recharts-tooltip-wrapper {
    visibility: visible!important;
  }
  .charts {
    display: flex;
    margin-top: 40px;
    justify-content: space-between;
    display: flex;
    .graph {
      width: 49%;
      background: rgba(85, 119, 253, 0.1);
      border: 1px solid #203B60;
      height: 440px;
      display: flex;
      align-items: flex-end;
    }
    .graph.volume24{
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
    }

  }
  .list-by-chain {
    margin-bottom: 112px;
    .table {
      font-size: 14px;
      .rc-table-cell:first-child{
        padding-left: 24px;
      }
      .rc-table-cell {
        min-width: 100px;
        text-align: left;
      }
      .rc-table-cell.base-token {
        span {
          height: auto!important;
          line-height: inherit!important;
        }
      }
    }
  }
}
@media only screen and (max-width: 760px) {
  .info-main {
    padding: 0 24px;
    margin-top: 24px;
    .title {
      font-size: 20px;
    }
    .charts {
      display: flex;
      margin-top: 0px;
      justify-content: space-between;
      display: flex;
      flex-direction: column;
      .graph {
        width: 100%;
        margin-top: 24px;
      }
    }
    .list-by-chain {
      margin-bottom: 112px;
      .table {
        .rc-table-cell:first-child{
          padding-left: 24px;
        }
        .rc-table-cell {
          min-width: 100px;
          text-align: left;
        }
      }
    }
  }
}