.pagination {
  height: 74px;
  border-top: 1px solid #203B60;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  .arrow {
    cursor: pointer;
  }
  .rc-pagination {
    display: flex;
    .rc-pagination-item a {
      color: #93A1B8;
    }
    .rc-pagination-item {
      color: #93A1B8;
      background-color: rgba(85, 119, 253, 0.1);
      border: 1px solid rgba(85, 119, 253, 0.1);
    }
    .rc-pagination-item.rc-pagination-item-active{
      background-color: rgba(85, 119, 253, 0.1);
      border: 1px solid rgba(205, 122, 55, 1);
    }
    .rc-pagination-item.rc-pagination-item-active a {
      color: #ffffff;
    }
    .rc-pagination-item:hover {
      border: 1px solid rgba(205, 122, 55, 0.5);
    }
  }
  .rc-pagination-options {
    display: flex;
    align-items: center;
    line-height: 26px;
    margin-left: 4px;
    .rc-pagination-options-size-changer.rc-select {
      width: 80px;
    }
    .rc-select-single:not(.rc-select-customize-input) .rc-select-selector {
      border: 1px solid #203B60;
    }
    .rc-select-focused {
      .rc-select-selector {
        border : 1px solid rgba(205, 122, 55, 1) !important;
      }
    }
    .rc-select-dropdown {
      background-color: #203B60;
      border : 1px solid #203B60;
    }
    .rc-select-item {
      padding: 0px;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .rc-select-item-option {
      border: 1px solid transparent;
      .rc-select-item-option-state {
        display: none;
      }
    }
    .rc-select-item-option-active {
      background-color: rgba(85, 119, 253, 0.1);
      border : 1px solid rgba(205, 122, 55, 0.5);
      cursor: pointer;
    }
    .rc-select-single .rc-select-selector .rc-select-selection-item, .rc-select-single .rc-select-selector .rc-select-selection-placeholder {
      font-weight: bold;
      color: #93A1B8;
      font-size: 14px;
      left : 6px
    }
    .rc-select-show-arrow .rc-select-arrow{
      right: 6px;
    }

    .rc-select-selector:hover,.rc-select-selection-search-input:hover {
      cursor: pointer;
    }
  }
}