.info-detail {
  .table {
    .rc-table-cell{
      .liquidate {
        border : 1px solid #FFFFFF;
        padding: 4px 16px;
        border-radius: 16px;
        color : #ffffff;
      }
      .short,.removeLiquidity{
        border : 1px solid #CE5E5E;
        color: #CE5E5E;
        padding: 4px 16px;
        border-radius: 16px;
      }
      .long,.addliquidity{
        border : 1px solid #59AE99;
        color: #59AE99;
        padding: 4px 16px;
        border-radius: 16px;
      }
    }
  }
}

@media only screen and (max-width: 760px) {
  .info-main {
    .list-by-chain {
      .table {
        .rc-table-cell:first-child{
          padding-left: 12px;
        }
        .rc-table-cell {
          min-width: 150px;
          text-align: left;
        }
      }
    }
  }
}